.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.root-container {
  padding: 1vh 1vw;
  margin: 1vh 0;
  height: 88vh !important;
  width: auto;
}

.sign-in-root-container {
  padding: 1vh 1vw;
  margin: 1vh 0;
  height: 88vh !important;
  width: auto;
  display: flex;
  align-items: center;
  justify-content: center;
}

.sign-in-root-paper {
  height: auto;
  width: 40vw;
}

.root-paper {
  height: 100%;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
